import React,{useState} from 'react'


export const Input1 = ({setCategories}) => {

    const [valor,setValor] = useState("escribe algo...")

    const handleValue = (e) =>{

setValor(e.target.value)
    }

    const handleSubmit =(e)=>{
        
            e.preventDefault();
            setCategories (cats =>[valor,...cats])
            console.log("hecho");
            setValor("")
    }

    return (
     <form onSubmit={handleSubmit}>

        <input value={valor} type="text" onChange={handleValue}></input>
        {/* <h2 >{valor}</h2> */}
        
     </form>
    )
}
